/**首页接口 */
import request from "@/utils/request"
//获取首页数据
export const getHomeData = () => {
  return request.post(`/school/home/index`)
}

//获取首页轮播图数据
export const getHomeImg = () => {
  return request.post(`/service/cms/pics/list/school_h5`, null, {
    custom: { // 自定义的额外参数
      isAis: true
    }
  })
}

//获取推荐课程
export const getHomeTops = () => {
  return request.post(`/school/home/tops`)
}
